'use strict';

var forEach = require(386);

var HANDLERS = {
  'properties-panel.update-businessobject': require(12),
  'properties-panel.create-and-reference': require(9),
  'properties-panel.create-businessobject-list': require(10),
  'properties-panel.update-businessobject-list': require(13),
  'properties-panel.multi-command-executor': require(11)
};


function CommandInitializer(eventBus, commandStack) {

  eventBus.on('diagram.init', function() {
    forEach(HANDLERS, function(handler, id) {
      commandStack.registerHandler(id, handler);
    });
  });
}

CommandInitializer.$inject = [ 'eventBus', 'commandStack' ];

module.exports = {
  __init__: [ CommandInitializer ]
};